export function getCookie(name: string) {
    let arr;
    const reg = new RegExp('(^|  )' + name + '=([^;]*)(;|$)');
    if (arr = document.cookie.match(reg)) {
        return arr[2];
    } else {
        return null;
    }
}

export function setCookie(name, value) {
    const Days = 30;
    const exp = new Date();
    exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
    document.cookie = name + '=' + value;
}



