import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { catchError, map, tap, retryWhen } from 'rxjs/operators';

import { serialize } from "../utils/serializer";
import { getCookie } from "../utils/cookies";
import { Observable, ObservableInput } from 'rxjs/Observable';
import { Router } from '@angular/router';


@Injectable()
export class LoginService {

  constructor(
    public http: HttpClient,
    public router: Router
  ) { }

  login(obj): any {
    return this.http.post('/api/core/login/', obj, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'X-CSRFToken': getCookie('csrftoken')
      })
    });
  }

  signup(obj): any {
    return this.http.post('/api/core/signup/', obj, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'X-CSRFToken': getCookie('csrftoken')
      })
    });
  }

  status(): any {
    return this.http.get('/api/core/status/', {
      headers: new HttpHeaders({
        'X-CSRFToken': getCookie('csrftoken')
      }),
    });
  }

  logout(): any {
    return this.http.get('/api/core/logout/', {
      headers: new HttpHeaders({
        'X-CSRFToken': getCookie('csrftoken')
      }),
    });
  }

  checkLoginStatus() {
    return this.status().subscribe(
      data => {
        if (data.status !== true){          
          this.router.navigate(['/auth/login']);
        }
      },
      err => {
        this.router.navigate(['/auth/login']);
      }
    ); 

  }

}



