import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoginService } from '../../services/login.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css'],
  providers: [LoginService]
})
export class RegisterComponent implements OnInit {
  username: string;
  password: string;
  querypass: string;
  error: string;
  panelString: string = 'already had a account?';

  constructor(
    public service: LoginService,
    private router: Router    
  ) { }

  ngOnInit() {
  }

  Register(username: string, password: string, querypass: string){
     this.error = '';
     if (this.password !== this.querypass){
       this.error = 'password not match the re-type password';
       this.password = '';
       this.querypass = '';
       return true;
     }
     this.service.signup(
       {
       username: this.username,
       password: this.password
      }
    ).subscribe(
       (res) => {
         if (res && res.status === true) {
          alert(`User ${this.username} register success!`);
          this.router.navigate(['/auth/login']);
         } else {
           this.error = res.msg;
           this.password = '';
           this.querypass = '';
         }
       }
     );
  }


  switchPanel() {
    this.router.navigate(['/auth/login']);
  }
}



